<template>
    <section class="">
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid mt-2 p-0">
            <div class="w-100">
                <div class="theme-primary partition-full">

                <!-- Show this button only when UtilityName is active -->
                <span class="p-actions float-right d-block text-center" v-if="isUtilityName">
                    <a class="p-action-btn text-white" @click="showQueueList">
                        <i class="fa fa-eye" aria-hidden="true"></i>
                        {{ 'ViewQueueList' }}
                    </a>
                </span>

                <!-- Show this button only when QueueList is active -->
                <span class="p-actions float-right tablehead" v-if="isQueueList">
                    <a class="p-action-btn text-white" @click="showUtility">
                        <i class="fa fa-angle-double-left"></i><br>
                        {{ $t('BacktoList') }}
                    </a>
                </span>

            </div>

            </div>
        </div>
        <div class="border p-3">
            <dynamic-form :schema="FormSchema" ref="ProcessCharges" :buttons="buttons" v-on:OnSubmit="onSubmit">
                
            </dynamic-form>
        </div>       

        <UtilityName v-if="isUtilityName" :ClientId="ClientId"></UtilityName>
        <QueueList v-if="isQueueList" :ClientId="ClientId"></QueueList>
        
    </section>
</template>

<script>


import DataService from '../../services/DataService';
import UtilityName from './UtilityName.vue';
import QueueList from './QueueList.vue';
export default {
     components: {
        UtilityName,
        QueueList
     },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                layoutType: "four",
                Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Contract/Customer Name'),
                            name: "CustomerName",
                            value: "",
                            visibility: true,
                            placeholder: "CustomerName",
                            disabled: false,
                            config: {
                                options: [],
                                onChange: this.ListingByClient
                            },
                            validationRules: {
                                "required": true
                            },
                        },       

                    ]
                },
            
            ],
           
            BillingRuleData: [],
            isCloneModel: false,
            listActions: [],
            listheaderbuttons: [],
            cloneItem: [],
            StatusListForListing: [
                {
                    STATUS: "Active",
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745",
                },
                {
                    STATUS: "Inactive",
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435",
                },
            ],
            Headers: [
                {
                    "COLUMN_NAME": "RULE_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BillingRuleName'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "SERVICE_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME":  this.$t('ServiceName'),
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": "CLIENT_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": false,
                    "DISPLAY_NAME": 'Contract',
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": "UTILITYNAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": false,
                    "DISPLAY_NAME": this.$t('UtilityName'),
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "CATEGORY_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": false,
                    "DISPLAY_NAME": 'Category',
                    "DISPLAY_ORDER": 4,
                },
                {
                    "COLUMN_NAME": "STATENAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": false,
                    "DISPLAY_NAME": this.$t('STATE'),
                    "DISPLAY_ORDER": 5,
                },
                {
                    "COLUMN_NAME": "EFFICATIVE_DATE_FROM",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EffectiveFrom'),
                    "DISPLAY_ORDER": 6,
                },
                {
                    "COLUMN_NAME": "EFFICATIVE_DATE_TO",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EffectiveTo'),
                    "DISPLAY_ORDER": 7,
                },
                {
                    "COLUMN_NAME": "STATUS_ID",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 8,
                },
                {
                    "COLUMN_NAME": "CREATED_BY_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CreatedBy'),
                    "DISPLAY_ORDER": 9,
                },
                {
                    "COLUMN_NAME": "CREATED_AT",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CreatedAt'),
                    "DISPLAY_ORDER": 9,
                },
                {
                    "COLUMN_NAME": "MODIFIED_BY_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ModifiedBy'),
                    "DISPLAY_ORDER": 9,
                },
                {
                    "COLUMN_NAME": "MODIFIED_AT",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ModifiedAt'),
                    "DISPLAY_ORDER": 9,
                }

            ],           
            leftSearchFields: [],
            searchByName: '',
            searchByClientName: '',
            utilities: '',
            searchByState: [],
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ListingData: [],
            servicePlanId: '',
            isBillingHistoryPopup:false,
            isUtilityName: true,
            isQueueList: false,
            ClientId:0

        }
    },
    created: async function () {
        var vm = this;
        vm.showUtility();       
        vm.GetGroupList();      
    },
    methods: {

        GetGroupList() {
            debugger;
            var vm = this;
            var url = `reqFrom=CUSTOM`;
            DataService.getClientList(url).then((response) => {
                debugger;
                if (response.data.items) {
                    if (response.data.items.length) {
                        response.data.items.forEach(element => {
                            var obj = {
                                name: element.text,
                                value: element.value
                            }
                            vm.FormSchema[0].Data[0].config.options.push(obj);
                        });
                         vm.$refs.ProcessCharges.UpdateKeyValue();
                    }
                }
            })
        },

        showUtility() {
            this.isUtilityName = true;
            this.isQueueList = false;
        },
        showQueueList() {
            this.isUtilityName = false;
            this.isQueueList = true;
        },       
        async ListingByClient(event, field, meta){                       
            this.ClientId=parseInt(field.value);                        
        }, 
                  

    }
}
</script>

<style lang="scss" scoped></style>