<template>
    <section class="">        

        <loader :is-visible="isLoading"></loader>

         <tg-list :IsShowAction="false" ModuleName="BillingRule" SubModuleCode="BillingRule" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="UTILITY_ID" :listType="['List']" :ListData="QueueData" :HeaderData="Headers"
             :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" 
            IsSearchFieldsOnTop="false" SearchPosition="" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :isShowHeaderText="false"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>

         <!-- :HeaderButtons="listheaderbuttons" -->
        
    </section>
</template>

<script>


import DataService from '../../services/DataService';
export default {
    props: {
        ClientId: {
            type: Number,
            require:false
        }
    },
    components: {},
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                layoutType: "four",
                Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Contract/Customer Name'),
                            name: "CustomerName",
                            value: "",
                            visibility: true,
                            placeholder: "CustomerName",
                            disabled: false,
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        },       

                    ]
                },
            
            ],
           
            QueueData: [],
            isCloneModel: false,
            listActions: [],
            listheaderbuttons: [],
            cloneItem: [],
            StatusListForListing: [
                {
                    STATUS: "Active",
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745",
                },
                {
                    STATUS: "Inactive",
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435",
                },
            ],
            Headers: [
                {
                    "COLUMN_NAME": "SERIAL_NUMBER",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SerialNumber'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "UTILITY_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('UtilityName'),
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": "TOTAL_RECORDS",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TotalRecords'),
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": "DATE_RANGE",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('DateRange'),
                    "DISPLAY_ORDER": 3,
                },
                {
                    "COLUMN_NAME": "REPROCESSED_BY",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ReprocessedBy'),
                    "DISPLAY_ORDER": 4,
                },
                {
                    "COLUMN_NAME": "REQUESTED_AT",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RequestedAt'),
                    "DISPLAY_ORDER": 5,
                },
                {
                    "COLUMN_NAME": "STATUS_ID",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 6,
                },                

            ],           
            leftSearchFields: [],
            searchByName: '',
            searchByClientName: '',
            utilities: '',
            searchByState: [],
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ListingData: [],
            servicePlanId: '',
            isBillingHistoryPopup:false
        }
    },
    
    watch: {
        ClientId() {
            debugger;
            this.FetchData();
        }
    },
    created: async function () {
        var vm = this;
        vm.FetchData();            
    },
    methods: {

        FetchData: async function () {
            debugger;
            var vm = this;
            vm.QueueData=[];
            var url = `clientId=${this.ClientId}&configId=&reqFrom=REPROCESSQUEUELIST&clientIds=`;           
            try {
                const response = await DataService.getUtilityList(url);
                if (response.data && response.data.length > 0) {   
                    debugger;        
                    vm.QueueData=response.data;       
                    vm.TotalRecords = response.data.length;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;

                } else {
                    vm.QueueData = [];
                    vm.TotalRecords = 0;
                    vm.noRecords = true;
                }

            } catch (error) {
                console.error("Error fetching data:", error);
            }
        },
       

      
        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                action.isVisible = true;
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchCondition) {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            debugger
            switch (callbackfunction) {
                
            }
        },       
        
        CloneRule(item) {
            debugger
            var vm = this;
            vm.isCloneModel = true;
            vm.GetUtility();
        },
        CloseCloneModel() {
            var vm = this;
            vm.$refs.validatePlan.updateProperty("RuleName", "value", "");
            vm.$refs.validatePlan.updateProperty("CompanyName", "value", "");
            vm.$refs.validatePlan.updateProperty("ContractName", "value", "");
            this.isCloneModel = false;
            this.cloneItem = [];
            this.FetchData();
        },

        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddBillingRule":
                    this.AddBillingRule();
                    break;
                
            }
        },       
        
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },         

    }
}
</script>

<style lang="scss" scoped></style>