<template>
<section class="">
    <loader :is-visible="isLoading"></loader>

    <tg-list :IsShowAction="true" ModuleName="BillingRule" SubModuleCode="BillingRule" :LegendArray="[]" :RenderRowActionMethod="renderActions" 
    v-on:ActionButtonClick="actionButtonClick" :widgets="[]" IdentityColumn="UTILITY_ID" :listType="['List']" :ListData="UtilityData" 
    :HeaderData="Headers" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" IsSearchFieldsOnTop="false" SearchPosition="" 
    :ListDataCallBackFunction="FetchData" :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" 
    :isShowHeaderText="false" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" 
    v-on:SortdataButtonClick="sortdata">
    <template v-slot:slotdata="{ data }">  
             <template v-if="data.column.COLUMN_NAME == 'EFFECTIVE_FROM'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.EFFECTIVE_FROM) }}
                    </span>
                </template>
                 <template v-if="data.column.COLUMN_NAME == 'EFFECTIVE_TO'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.EFFECTIVE_TO) }}
                    </span>
                </template> 
    </template> 
    </tg-list>
    <!-- :HeaderButtons="listheaderbuttons"  -->

</section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        ClientId: {
            type: Number,
            require: false
        }
    },

    components: {},

    data() {
        return {
            isLoading: false,
            FormSchema: [{
                    layoutType: "four",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Contract/Customer Name'),
                            name: "CustomerName",
                            value: "",
                            visibility: true,
                            placeholder: "CustomerName",
                            disabled: false,
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        },

                    ]
                },

            ],

            UtilityData: [],
            isCloneModel: false,
            listActions: [],
            listheaderbuttons: [],
            cloneItem: [],
            StatusListForListing: [{
                    STATUS: "Active",
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745",
                },
                {
                    STATUS: "Inactive",
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435",
                },
            ],
            Headers: [
                {
                    "COLUMN_NAME": "SERIAL_NUMBER",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SerialNumber'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "UTILITY_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('UtilityName'),
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": "EFFECTIVE_FROM",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EffectiveFrom'),
                    "DISPLAY_ORDER": 3,
                     settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "EFFECTIVE_TO",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EffectiveTo'),
                    "DISPLAY_ORDER": 4,
                     settings: {
                        isInSlot: true
                    }
                },
                // {
                //     "COLUMN_NAME": "Action",
                //     "DATA_TYPE": "text",
                //     "SORTABLE": true,
                //     "VISIBLE": false,
                //     "DISPLAY_NAME": 'Action',
                //     "DISPLAY_ORDER": 4,
                // },             

            ],
            leftSearchFields: [],
            searchByName: '',
            searchByClientName: '',
            utilities: '',
            searchByState: [],
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ListingData: [],
            servicePlanId: '',
            isBillingHistoryPopup: false
        }
    },

    watch: {
        ClientId() {
            debugger;
            this.FetchData(); 
            this.SaveProcessReprocessCharge();          
        }
    },
    created: async function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {

         SaveProcessReprocessCharge: async function () {
            var vm = this;
            var date = this.$options.filters.formatDate(new Date());
            var param = `clientId=${vm.ClientId}&additionalId=${vm.ClientId}&dateFrom=${date}&dateTo=${date}`;

            try {
                const response = await DataService.SaveProcessReprocessCharge(param);
                if (response.data.result) {
                    const Result = response.data.result.data;
                    if (Result.STATUS_CODE == 0) {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Process')]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    } else if (Result.STATUS_CODE == 1) {
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Process')]), "Success", true, vm.$t('Alert'));
                        vm.Close();
                    }
                    else if (Result.STATUS_CODE == 2) {
                         vm.$router.push({path: '/ClientBill/Index'});
                        vm.Close();
                    } 
                    else if (Result.STATUS_CODE == 3) {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('invoiceNumberList')]), "failure", vm.$t('Alert'));
                        vm.Close();
                    }
                    else if (Result.STATUS_CODE == 4) {
                        vm.$router.push({path: '/ClientBill/Index'});
                        vm.Close();
                    } 
                    else {
                        vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));                    
                    }
                }

            } catch (error) {
                console.error("Error fetching data:", error);
            }
        },   

        FetchData: async function () {
            debugger;
            var vm = this;
            vm.UtilityData=[];
            var url = `clientId=${this.ClientId}&configId=&reqFrom=reprocesscharge&clientIds=`;           
            try {
                const response = await DataService.getUtilityList(url);
                if (response.data && response.data.length > 0) {   
                    debugger; 
                    vm.UtilityData=response.data;       
                    vm.TotalRecords = response.data.length;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;

                } else {
                    vm.UtilityData = [];
                    vm.TotalRecords = 0;
                    vm.noRecords = true;
                }

            } catch (error) {
                console.error("Error fetching data:", error);
            }
        },

       

        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                action.isVisible = true;
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },

        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchCondition) {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        actionButtonClick: function (callbackfunction, item, index, event) {
            debugger
            switch (callbackfunction) {

            }
        },

        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddBillingRule":
                    this.AddBillingRule();
                    break;

            }
        },

        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

    }
}
</script>

<style lang="scss" scoped></style>
